import React,{useEffect,useState} from 'react'
import apiurl from "./apiurl"
import authfetch from './authfetch'
import { useNavigate } from 'react-router-dom'
import Alert from './Alert'


export default function Add() {
  const navigate = useNavigate()
  const [monthlyMoney, setmonthlyMoney] = useState(undefined)
  const [at, setAt] = useState(undefined)
  const [members,setMembers] = useState(null)

  useEffect(() => {
    authfetch(`${apiurl}/installment`,navigate,{method:"GET"})
    .then(resp=>resp.json())
    .then(data=>{
      setmonthlyMoney(data.value.money)
    })
    .catch(err=>console.log(err))
    
    //meeting place
    authfetch(`${apiurl}/meeting`,navigate,{method:"GET"})
    .then(resp=>resp.json())
    .then(data=>setAt(data.data.name))

    // fetch members
    authfetch(`${apiurl}/members`,navigate,{method:"GET"})
    .then(resp=>resp.json())
    .then(data=>setMembers(data))
    // eslint-disable-next-line
  }, [])

  const checkAllUsers = () =>{
    const switches = document.querySelectorAll(".memNameCheckbox")
    switches.forEach(element => {
      element.checked = (element.checked) ? false: true; 
    });
  }

  const addRecords = async (event) =>{
    event.preventDefault()
    const form = document.querySelector("form")
    if (!form.checkValidity()){
      form.reportValidity()
      return
    }
    let users = document.querySelectorAll('.memNameCheckbox')
    users = Array.from(users).filter((elem)=>{return elem.checked}) //filter unchecked <input>
    const presentUserNames = users.map((elem)=>{return elem.value}) // extract the checked names
    const monthdata = presentUserNames.map((name)=>{
      return {
        name:name,
        amt:monthlyMoney,
        fine:0,
        status:"present"
      }
    })
    const payload = {
      nepalidate:document.querySelector("#datenow").value,
      at:at,
      monthdata:monthdata
    }
    console.log(payload)
    await authfetch(`${apiurl}/monthly`,navigate,{
      method:"POST",
      body: JSON.stringify(payload)
    }).then(resp=>resp.json())
    .then(data=>document.querySelector(".show-alert").innerHTML = Alert(data.message))
    .catch(err=>{
      console.log("err",err)
      document.querySelector(".show-alert").innerHTML = "failed to add data"
    })

  }

  return (
    <>
    <div className="container">
      <h2 className='text-center mt-3'>Add Logs</h2>
      <p>Monthly Price: {monthlyMoney}</p>
      <p>At: {at}</p>
      <form>
      <div className="input-group mb-3">
        <span className="input-group-text" id="basic-addon1">date</span>
        <input type="text" className="form-control" id="datenow" placeholder="date" aria-label="date" aria-describedby="basic-addon1" style={{width:"40%",flex:"none"}} required/>
      </div>
      <p>Received From:</p>
      <div className="form-check form-switch">
        <input className="form-check-input" onChange={checkAllUsers} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Check / Uncheck all</label>
      </div>
      {members && members.map(member=>(
        <div className="form-check form-switch" key={member._id}>
          <input className={`form-check-input memNameCheckbox`} type="checkbox" role="switch" id="flexSwitchCheckDefault" value={member.name} />
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{member.name}</label>
        </div>
      ))}
      <button className="btn btn-outline-primary px-5 mt-4" onClick={addRecords}>Add</button>
      </form>

    </div>    
    </>
  )
}
