import React,{useState,useEffect} from 'react'
import { useNavigate } from 'react-router-dom'
import authfetch from './authfetch'
import apiurl from "./apiurl"
import Alert from './Alert'



function DeleteMonth(props){
  const deleteMonth = () =>{
    const monthId = document.querySelector(".monthselect").value
    authfetch(`${apiurl}/monthly/`,props.navigate,{
      method:"DELETE",
      body:JSON.stringify({
        _id:monthId
      })
    })
    .then((resp)=>{
      if (resp.status === 204){
        document.querySelector(".show-alert").innerHTML = Alert(`Deleted successfully ${monthId}`,'success')
        props.setDeletedId(monthId)

      }
    })

    .catch((err)=>{
      console.log(err)
      document.querySelector(".show-alert").innerHTML = Alert("failed to insert data","danger")
    })
  }

  return (
    <>
      <div className='row'>
        <div className='col-sm-4'>
      <select className='form-select monthselect' data-bs-theme="dark">
        {props.months && props.months.map(elem=>(
          <option value={elem._id} key={elem._id}>{elem.nepalidate}</option>
        ))}
      </select>


        </div>
        <div className='col'>
      <button className='btn btn-primary' onClick={deleteMonth}>delete</button>

        </div>
      </div>
    </>
  )
}


function ShiftMeeting(props){
  const [turn, setTurn] = useState(null)

  useEffect(()=>{
    authfetch(`${apiurl}/meeting`,props.navigation,{
      method:"GET"
    }).then(resp=>resp.json())
    .then(data=>setTurn(data.data))
    .catch(err=>console.log(err))

    
    // eslint-disable-next-line
  },[])

  const shift = () =>{
    const shiftTo = document.querySelector(".memberselect").value
    // eslint-disable-next-line no-restricted-globals
    if (confirm(`shift to ${props.members.filter(elem=>elem._id === shiftTo)[0].name}?`)){
      authfetch(`${apiurl}/meeting/shift`,props.navigation,{
        method:"PUT",
        body:JSON.stringify({shiftTo})
      }).then(resp=>{
        if (resp.status === 201){
          document.querySelector(".show-alert").innerHTML = Alert("Shift successful","success")
          setTurn(Object.assign({},turn || {},props.members.filter(elem=>elem._id === shiftTo)[0]))
        }
      })
      .catch(err=>console.log(err))
    }
  }

  return (
    <>
      Next Meeting: {turn && turn.name}<br />
      Shift To: 
      <div className='row'>
        <div className='col-sm-4'>
          <select className='form-select text-black memberselect'>
            {props.members && props.members.map(elem=>(
              <option value={elem._id} key={elem._id}>{elem.name}</option>
            ))}
          </select>
        </div>
        <div className="col">
          <button className='btn btn-primary' onClick={shift}>shift</button>

        </div>
      </div>
    </>
  )
}

function ChangePassword(){
  const [loginUsers,setLoginUsers] = useState(null)
  const navigate = useNavigate()
  useEffect(() => {
    authfetch(`${apiurl}/loginusers`,navigate)
    .then(resp=>resp.json())
    .then(data=>setLoginUsers(data))
    .catch(err=>console.log(err))
    // eslint-disable-next-line
  }, [])

  const changepasswd = (event) => {
    event.preventDefault()
    const form = document.querySelector(".formPwChange")
    if (!form.checkValidity()){
      form.reportValidity()
      return
    }
    const username = document.querySelector(".loginuserselect").value
    const oldpass = document.querySelector(".oldpass").value
    const newpass = document.querySelector(".newpass").value
    let status
    authfetch(`${apiurl}/loginusers`,navigate,{
      method:"PUT",
      body:JSON.stringify({
        _id:username,
        oldpassword:oldpass,
        newpassword:newpass
      })
    }).then(resp=>{
      status = resp.status
      return resp.json()
    })
    .then(data=>document.querySelector(".show-alert").innerHTML = Alert(data.message,(status === 200)? "success":"warning"))
    .catch(err=>{
      console.log(err)
      document.querySelector(".show-alert").innerHTML = Alert("error occured","warning")
    })
    



  }

  const monitorpass = () =>{
    console.log("monitor pass is running")
    const newpass = document.querySelector(".newpass").value
    const confirmpass = document.querySelector(".confirmpass").value

    if (newpass !== confirmpass || confirmpass === ""){
      document.querySelector(".confirmPassText").innerHTML = "new and confirm password doesnt match"
      document.querySelector(".changebtn").classList.add("disabled")
    }else{
      document.querySelector(".confirmPassText").innerHTML = ""
      document.querySelector(".changebtn").classList.remove("disabled")


    }

  }

  return (
    <>
      <div>
        <form className='formPwChange'>
        <div className='row'>
          <div className='col-sm-2'>
            <select className='form-select loginuserselect' defaultValue="" required>
              <option value="">select any</option>
              {loginUsers && loginUsers.map(elem=>(
                <option value={elem._id} key={elem._id}>{elem.username}</option>
              ))}
            </select>
          </div>
          
          
        </div>
        <div className='col-sm-2 my-2'>
            <input className='form-control oldpass' placeholder='old password' type='password' required id="exampleInputPassword1"></input>

        </div>
        <div className='col-sm-2 my-2'>
            <input className='form-control newpass' placeholder='new password' type='password' required></input>
        </div>
        <div className='col-sm-2 my-2'>
            <input className='form-control confirmpass' required placeholder='confirm password' type='password' onChange={monitorpass}></input>
            <span className='confirmPassText'></span>
        </div>
        <div className='col'>
          
            <button className="btn btn-primary changebtn disabled" onClick={changepasswd}>change password</button>
          </div>
          </form>
      </div>
    </>
  )
}

function SomebodyMissed(props){
  let status

  const addMissedMembers = (event) =>{
    event.preventDefault()
    const form = document.querySelector(".addMissedForm")
    if (!form.checkValidity()){
      form.reportValidity()
      return
    }
    const name = document.querySelector(".memberselect2").value
    const ref_month = document.querySelector(".dateselect2").value
    const fine = document.querySelector(".fine").value
    const amt = props.installmentPerMonth.money
    
    authfetch(`${apiurl}/addmissed`,props.navigate,{
      method:"POST",
      body: JSON.stringify({
        name,ref_month,fine,amt
      })
    }).then(resp=>{
      status = resp.status
      return resp.json()      
    }).then(data=>{
      document.querySelector(".show-alert").innerHTML = Alert(data.message,(status === 200)? "success":"danger")
    })
    .catch(err=>console.log(err))

  }

  return (
    <>
    <form className='addMissedForm'>
    <div className='row'>
      <div className='col col-sm-4'>
        <select className='form-select memberselect2' required>
          <option value="">select members</option>
          {props.members && props.members.map(elem=>(
            <option value={elem.name} key={elem._id}>{elem.name}</option>
          ))}
        </select>
      </div>
      <div className='col col-sm-4'>
        <select className='form-select dateselect2' required>
          <option value="">select month</option>
          {props.months && props.months.map(elem=>(
            <option value={elem._id} key={elem._id}>{elem.nepalidate}</option>
          ))}

        </select>
      </div>
    </div>
    <div className='row my-2'>
      <div className='col-sm-4'>
        <input className='form-control fine' placeholder='fine' type='number'></input>
      </div>
      <div className='col-sm-4'>
        <button className="btn btn-primary" style={{width:"100%"}} onClick={addMissedMembers}>add record</button>
      </div>
    </div>
    </form>
    </>
  )
}

export default function Settings() {
  const [months, setMonths] = useState(null)
  const [deletedId,setDeletedId] = useState(undefined)
  const [installmentPerMonth, setInstallmentPerMonth] = useState(null)
  // eslint-disable-next-line
  const [members, setMembers] = useState(null)
  const navigate = useNavigate()

  useEffect(() => {
    authfetch(`${apiurl}/monthly`,navigate,{method:"GET"})
    .then(resp=>resp.json())
    .then(data=>setMonths(data.data))
    .catch(err=>console.log(err))

    authfetch(`${apiurl}/installment`,navigate)
    .then(resp=>resp.json())
    .then(data=>setInstallmentPerMonth(data.value))

    authfetch(`${apiurl}/members`,navigate,{
      method:"GET"
    }).then(resp=>resp.json())
    .then(data=>setMembers(data))
    .catch(err=>console.log(err))

  }, [deletedId,navigate])

  const updateInstallment = () =>{
    const newMoney = document.querySelector("#newinstallment").value
    const payload = {_id:installmentPerMonth._id,money:newMoney}
    authfetch(`${apiurl}/installment`,navigate,{
      method:"PUT",
      body: JSON.stringify(payload)
    }).then(resp=>resp.json())
    .then(data=>{
      setInstallmentPerMonth(payload)
      document.querySelector(".show-alert").innerHTML = Alert(data.message,"success")
    })
    .catch(err=>console.log(err))
  }
  const logout = () =>{
    localStorage.clear()
    navigate("/")
  }
  return (
    <>
    
    <div className='container'>
      <div className="accordion" id="accordionExample" data-bs-theme="dark">
        {/* update installment */}
        <div className="accordion-item">
          <h2 className="accordion-header">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="false" aria-controls="collapseOne">
              Change Monthly Money
            </button>
          </h2>
          <div id="collapseOne" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div className="accordion-body">
              <p> Installment per month: {installmentPerMonth &&  installmentPerMonth.money}</p>
              <div className="input-group mb-3">
                <span className="input-group-text" id="basic-addon1">@</span>
                <input type="text" className="form-control" placeholder="Username" aria-label="New Monthly installment" aria-describedby="basic-addon1" id="newinstallment" />
              </div>
              <button className='btn btn-primary' onClick={updateInstallment}>Update</button>
            </div>
          </div>
        </div>
        {/* delete monthly data */}
        <div className="accordion-item">
          <h2 className="accordion-header">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
              Delete Month
            </button>
          </h2>
          <div id="collapseTwo" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div className="accordion-body">
              <DeleteMonth months={months} setDeletedId={setDeletedId} navigate={navigate} />
            </div>
          </div>
        </div>
        {/* shift meeting */}
        <div className="accordion-item">
          <h2 className="accordion-header">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
              Shift meeting
            </button>
          </h2>
          <div id="collapseThree" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div className="accordion-body">
              <ShiftMeeting members={members} />
            </div>
          </div>
        </div>
        {/* change password */}
        <div className="accordion-item">
          <h2 className="accordion-header">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseFour" aria-expanded="false" aria-controls="collapseThree">
              Change password
            </button>
          </h2>
          <div id="collapseFour" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div className="accordion-body">
              <ChangePassword />
            </div>
          </div>
        </div>
        {/* somebody missed */}
        <div className="accordion-item">
          <h2 className="accordion-header">
            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseFive" aria-expanded="false" aria-controls="collapseThree">
              Somebody Missed?
            </button>
          </h2>
          <div id="collapseFive" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
            <div className="accordion-body">
              <SomebodyMissed months={months} members={members} installmentPerMonth={installmentPerMonth} />
            </div>
          </div>
        </div>

      </div>
      <button className='btn btn-primary' onClick={logout}>logout</button>
    </div>
    </>
  )
}
