import React, { useEffect, useState } from 'react'
import apiurl from "./apiurl"
import authfetch from './authfetch'
import { useNavigate } from 'react-router-dom'
import Alert from "./Alert"
import NepaliDate from "nepali-date"

const ajakodate = new NepaliDate(new Date())


function CalculateTotalPaidAndRemainin(props){
  if (props.currentMemData){
    console.log(props.currentMemData,"is the current mem data")
  }
  // if (props.individualLoan){
  //   console.log("the indivudial loan data is ",props.individualLoan)
  // }
  return (
    <>
    {props.currentMemData &&
      <span>so far, <strong>{props.currentMemData.name }</strong> has paid Rs. {props.currentMemData.totalPaid} with intrest Rs. {props.currentMemData.totalIntrestPaid}<br />
      left to pay : {props.currentMemData.currentLoanInfo.remaining}
      </span>
    }
    
    </>
  )
}

export default function Loans(props) {
  const [loans, setLoans] = useState(null)
  const navigate = useNavigate()
  const [members, setMembers] = useState(null)
  const [individualLoan,setIndividualLoan] = useState(null)
  const [intrestRate, setintrestRate] = useState(0)
  let status;
  const [currentMemData,setCurrentMemLoanData] = useState(null)
  const [reload,setReload] = useState(new Date()) // used date because it will be random each time 
  
  
  
  useEffect(() => {
    authfetch(`${apiurl}/members`,navigate,{method:"GET"})
      .then(resp=>resp.json())
      .then(data=>setMembers(data))
      .catch(err=>console.log(err))
    
    authfetch(`${apiurl}/loans/intrestrate`,navigate)
    .then(resp=>resp.json())
    .then(data=>setintrestRate(data.rate))
    .catch(err=>console.log(err))

    authfetch(`${apiurl}/loans`,navigate)
    .then(resp=>resp.json())
    .then(data=>{
      setLoans(data.data)
      console.log(data.data)
    })
    .catch(err=>console.log(err))
  
  }, [navigate,reload])

  const processLoan = () =>{
    const name = document.querySelector(".loanMemField").value
    const amount = document.querySelector(".loanAmtField").value
    const date = document.querySelector(".loanDateField").value
    const form = document.querySelector(".takeLoanForm")
    if (!form.checkValidity()){
      form.reportValidity()
      return
    }
    // get previous uncleared loan of selected user (if exists)
    const prevLoanOfUser = loans.filter(elem=>elem.name===name && elem.cleared === false)
    if (prevLoanOfUser.length > 0){
      // if prev uncleared loan exists then update the loan and add the cuurent loan to previous one
      authfetch(`${apiurl}/loans/append`,navigate,{
        method:"PUT",
        body: JSON.stringify({
          ref_loan:prevLoanOfUser[0]._id,
          name,date,amount
        })
      }).then(resp=>resp.json())
        .then(data=>{
          document.querySelector(".show-alert").innerHTML = Alert(data.message,"success")
          setReload(new Date())
        })
        .catch(err=>console.log(err))
    }
    else{
      authfetch(`${apiurl}/loans`,navigate,{
        method:"POST",
        body:JSON.stringify({
          name:name,
          loanTakenDate:date,
          amount:amount
        })
      }).then(resp=>resp.json())
      .then(data=>{
        document.querySelector(".show-alert").innerHTML = Alert(data.message,"success")
        setReload(new Date())
      })
      .catch(err=>console.log(err))
    }
  }

  const loadIndividualLoan = (_id) =>{
    authfetch(`${apiurl}/loans/get/${_id}`,navigate)
    .then(resp=>resp.json())
    .then(resp=>{
      setIndividualLoan(resp.data)


      setCurrentMemLoanData({
        name: loans.filter(elem=>elem._id === _id)[0].name,
        totalPaid:resp.data.reduce((acc, elem) => acc + elem.amount, 0),
        totalIntrestPaid: resp.data.reduce((acc, elem) => acc + elem.intrest, 0),
        currentLoanInfo:loans.filter(elem=>elem._id === _id)[0]
      })
      console.log("current info",currentMemData)
      // document.querySelector(".totalData").innerHTML = `<td>
      //   <strong>total</strong></td>
      //   <td>${totalPaid}</td>
      //   <td>${totalIntrestPaid}</td>`
    })
    .catch(err=>console.log(err))

    // console.log(individualLoan)
  }

  const handleMemberSelect = (event) => {
    // when user is selected , gets the name, filter the loans by name and cleared=false(ie uncleared loan of user)
    // then fills the date in <select>
    const name = event.target.value
    console.log(loans.filter(elem=>elem.name===name))
    const filteredLoans = loans.filter(elem=>elem.name === name && elem.cleared === false).map(elem=>(
        `<option value="${elem._id}">${elem.loan_taken_dates}</option>`      
    ))
    document.querySelector(".loanDateSelect").innerHTML = "<option>select date</option>"+filteredLoans.join("")
  }

  const payLoan = () => {
    // const name = document.querySelector(".payLoanMem").value
    const date_ref_loan = document.querySelector(".loanDateSelect").value // this contain _id of loantaken
    const todayDate = document.querySelector(".todayDate").value
    const intrest = document.querySelector(".intrestField").value
    const paybackamt = document.querySelector(".payBackAmt").value || 0
    authfetch(`${apiurl}/loans/payloan`,navigate,{
      method:"POST",
      body: JSON.stringify({
        ref_loan: date_ref_loan,
        date: todayDate,
        amount:paybackamt,
        intrest:intrest
      })
    }).then(resp=>{
      status = resp.status
      return resp.json()
    })
    .then(data=>{
      console.log(data)
      document.querySelector(".show-alert").innerHTML = Alert(data.message,(status === 200)?"success":"danger")
      setReload(new Date())
    })
    .catch(err=>{
      console.log(err)
      document.querySelector(".show-alert").innerHTML = Alert("some error occured..","danger")
    })
  }

  const calculateIntrest = (event) =>{
    const name = document.querySelector(".payLoanMem").value
    console.log("the name is ",name)
    try {
      const filteredLoans = loans.filter(elem=>elem.name===name && elem.cleared===false)[0] //only one data will be left
      const intrest = Number(event.target.value) * Math.round((intrestRate / 1200 * filteredLoans.remaining))
      document.querySelector(".intrestFieldDiv").style.display = ""
      document.querySelector(".intrestField").value = intrest
      
    } catch (error) {
      alert("select all data before this field")
      event.target.value = ""
    }
  }

  return (
    <>
    <div className="container-fluid">
      <button type="button" className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#takeloanModal">
      Take Loan
      </button>

      <button type="button" className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#payloanModal">
      Give Back
      </button>
    </div>

    <div className="text-center d-date text-light">
    <div className="accordion" id="accordionExample">
  <div className="accordion-item">
    <h2 className="accordion-header">

      <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
        Ongoing
      </button>
    </h2>
    <div id="collapseOne" className="accordion-collapse collapse show" data-bs-parent="#accordionExample">
      <div className="accordion-body">
      {loans && loans.filter(elem=>elem.cleared===false).map(elem=>(
        <div key={elem._id}>
        <button className="m-log-btn" data-bs-toggle="modal" data-bs-target="#individualLoanModal" onClick={()=>loadIndividualLoan(elem._id)}>{elem.name} ({elem.loan_taken_dates.join(", ")})</button>
        
        <hr />
        </div>
        
      ))}
       
        
        
      </div>
    </div>
  </div>
  <div className="accordion-item">
    <h2 className="accordion-header">
      <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
        Cleared
      </button>
    </h2>
    <div id="collapseTwo" className="accordion-collapse collapse" data-bs-parent="#accordionExample">
      <div className="accordion-body">
      {loans && loans.filter(elem=>elem.cleared===true).map(elem=>(
        <div key={elem._id}>
        <button className="m-log-btn" data-bs-toggle="modal" data-bs-target="#individualLoanModal" onClick={()=>loadIndividualLoan(elem._id)}>{elem.name} ({elem.loan_taken_dates.join(", ")})</button>
        
        <hr />
        </div>
        
      ))}
</div>
      </div>
    </div>
  </div>

</div>


      {/* take loan modal */}
      <div className="modal fade" id="takeloanModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5" id="exampleModalLabel">Take Loan</h1>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
              Intrest Rate: {intrestRate}
              <form className='takeLoanForm'>
              <select className='form-select mb-3 loanMemField' required>
                <option value="">select any</option>
                {members && members.map(elem=>(
                  <option value={elem.name} key={elem._id}>{elem.name}</option>
                ))}
              </select>

              <div className="input-group mb-3">
                <span className="input-group-text" id="basic-addon1">
                <i className="fas fa-hand-holding-usd" aria-hidden="true"></i>
                </span>
                <input type="number" className="form-control loanAmtField" placeholder="loan Amount" aria-label="Username" aria-describedby="basic-addon1" required />
              </div>
              <div className="input-group mb-3">
                <span className="input-group-text" id="basic-addon1">
                <i className="fas fa-calendar-alt" aria-hidden="true"></i>
                </span>
                <input type="text" className="form-control loanDateField" placeholder="date" aria-label="Username" aria-describedby="basic-addon1" required/>
              </div>
              </form>

            </div>
            <span>*appended to already existing uncleared loan (if exists)</span>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary" onClick={processLoan}>Take loan</button>
            </div>
          </div>
        </div>
      </div>

      {/* pay loan modal */}
      <div className="modal fade" id="payloanModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5" id="exampleModalLabel">Pay Loan</h1>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
              <form className='payLoanForm'>
                <select  className='form-select payLoanMem mb-3' required onChange={(event)=>handleMemberSelect(event)}>
                  <option selected disabled >select member</option>
                  {/* show name for only uncleared loans */}
                  {loans && loans.filter(elem=>elem.cleared===false).map(elem=>(
                    <option value={elem.name} key={elem._id}>{elem.name}</option>
                  ))}
                </select>
                
                <select className='form-select loanDateSelect mb-3'>
                  <option disabled>loan taken date</option>
                  <option disabled>select member first</option>
                </select>

                <div className='input-group mb-3'>
                <span className='input-group-text'><i className="fas fa-hand-holding-usd" aria-hidden="true"></i></span>
                <input className='form-control todayDate' placeholder='today date' defaultValue={ajakodate.format("YYYY/MM/DD")}></input>
                </div>
                <div className='input-group mb-3'>
                <span className='input-group-text'><i className="fas fa-hand-holding-usd" aria-hidden="true"></i></span>
                <input className='form-control payBackAmt' placeholder='pay back amount'></input>
                </div>

                <div className='input-group mb-3'>
                  <span className='input-group-text'><i className="fas fa-calendar-alt" aria-hidden="true"> </i> </span>
                  <input className='form-control' placeholder='kati mahina ko intrest' onChange={(event)=>calculateIntrest(event)}></input>
                </div>
                <div className='input-group mb-2 intrestFieldDiv' style={{display:"none"}}>
                  <span className='input-group-text'>Intrest</span>
                  <input placeholder='intrest d-none' className='form-control intrestField'></input>
                </div>
              </form>
            </div>
            <span>*formula used: month * ({intrestRate} / 1200 * remaining) and rounded</span>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary" data-bs-dismiss="modal" onClick={payLoan}>Pay loan</button>
            </div>
          </div>
        </div>
      </div>

      {/* show individual loan data modal */}
      <div className="modal fade" id="individualLoanModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5" id="exampleModalLabel">Indivudial Loan</h1>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div className="modal-body">
              <table className="table table-striped">
                <thead>
                  <tr>
                    <td>date</td>
                    <td>amount</td>
                    <td>intrest</td>
                  </tr>
                </thead>
                <tbody>
                  {individualLoan && individualLoan.map(elem=>(
                    <tr key={elem._id}>
                      <td>{elem.date}</td>
                      <td>{elem.amount}</td>
                      <td>{elem.intrest}</td>
                    </tr>
                  ))}
                  <tr className='totalData'>
                    <td><strong>Total</strong></td>
                    <td>{currentMemData && currentMemData.totalPaid}</td>
                    <td>{currentMemData && currentMemData.totalIntrestPaid}</td>
                  </tr>
                </tbody>
              </table>
              {/* <span>so far user paid {totalPaid} with intrest {totalIntrestPaid}</span> */}
              <CalculateTotalPaidAndRemainin currentMemData={currentMemData} individualLoan={individualLoan} />

            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary">Save changes</button>
            </div>
          </div>
        </div>
      </div>

    </>
  )
}
