import React,{useState,useEffect} from 'react';
import { useParams } from 'react-router-dom';
import apiurl from './apiurl'
import authfetch from './authfetch';
import { useNavigate } from 'react-router-dom';


export default function Individual() {
  const [name, setName] = useState(useParams().name)
  const [data,setData] = useState(null)
  const [membersList,setMembersList] = useState(null)
  const navigate = useNavigate()

  function fetchIndividualData(){
    authfetch(`${apiurl}/members/individual/${name}`,navigate,{method:"GET"})
    .then(resp=>resp.json())
    .then(data=>setData(data.data))
    .catch(err=>console.log(err))
  }

  function fetchMembers(){
    authfetch(`${apiurl}/members`,navigate,{method:"GET"})
    .then(resp=>resp.json())
    .then(data=>{
      setMembersList(data)
    })
    .catch(err=>console.log(err))
  }
  
  
  useEffect(() => {
    fetchIndividualData()
    fetchMembers()
    console.log("use effect is running")
    // eslint-disable-next-line
  }, [name])
  return (
    <>
    <div className='container'>
    <select name="members" id="members" onChange={(event)=>setName(event.target.value)}>
      {membersList && membersList.map(elem=>(
        <option value={elem.name} selected={elem.name === name}>{elem.name}</option>
      ))}
    </select>
        <h1>{name}</h1>
    <table className="table">
      <thead>
        <tr>
          <td>id</td>
          <td>date</td>
          <td>amount</td>
          <td>fine</td>
        </tr>
      </thead>
      <tbody>
        {data && data.map(elem=>(
          <tr>
            <td>{data.indexOf(elem)}</td>
            <td>{elem.ref_month.nepalidate}</td>
            <td>{elem.amt}</td>
            <td>{elem.fine}</td>
          </tr>
        ))}
      </tbody>
    </table>
    </div>
    </>
  );
}