import React,{useState,useEffect} from 'react'
import apiurl from "./apiurl"
import authfetch from './authfetch'
import { useNavigate } from 'react-router-dom'


export default function MonthlyLogs() {
  const navigate = useNavigate()
  const [allMonthly, setSetAllMonthly] = useState(null)
  const [monthly, setMonthly] = useState(null)
  const [at,setAt] = useState(null)

  async function fetchMonthlyData(){
    const resp = await authfetch(`${apiurl}/monthly`,navigate,
    {
      method:"GET"
    });
    const data = await resp.json()
    console.log(data.data)
    setSetAllMonthly(data.data)
  }
  // when click get the month _id and fetch Monthly data
  function handleClick(data) {
    setAt({"at":data.at,"nepalidate":data.nepalidate})
    authfetch(`${apiurl}/monthly/${data._id}`,navigate,{method:"GET"})
    .then(resp=>resp.json())
    .then(data=>setMonthly(data.data))
    .catch(err=>console.log(err))
  }
  
  useEffect(() => {
    fetchMonthlyData()
    // eslint-disable-next-line
  }, [])

  

  console.log("rendered monthlylogs")
  return (
    <>
    <div className="container text-center position-relative">
      <div className="position-absolute start-50 monthlybox p-3 rounded" style={{transform:"translate(-50%,20%)",width:"80%"}}>
        {allMonthly && allMonthly.map(elem=>(
          <div className='mb-1' key={elem._id}>
          <button onClick={()=>{handleClick(elem)}} className='btn btn-outline-light' data-bs-toggle="modal" data-bs-target="#exampleModal">{elem.nepalidate}</button>
          </div>
        ))}
      </div>
    </div>
    {/* for modal */}


<div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div className="modal-dialog">
    <div className="modal-content">
      <div className="modal-header">
        <h1 className="modal-title fs-5" id="exampleModalLabel">Monthly Log of <i>{at && at.nepalidate}</i> at <strong>{at && at.at}</strong></h1>
        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div className="modal-body">
        <table className="table">
          <thead>
            <tr>
              <td>id</td>
              <td>name</td>
              <td>amount</td>
              <td>fine</td>
            </tr>
          </thead>
          <tbody>
            {monthly && monthly.map(elem=>(
              <tr key={monthly._id}>
                <td>{monthly.indexOf(elem) + 1}</td>
                <td>{elem.name}</td>
                <td>{elem.amt}</td>
                <td>{elem.fine}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" className="btn btn-primary">Save changes</button>
      </div>
    </div>
  </div>
</div>
    </>
  )
}
