import React from 'react'
import apiurl from './apiurl'
import { useNavigate } from 'react-router-dom'

export default function Login() {
    const navigate = useNavigate()
    if (localStorage.getItem("token")){
        navigate("/monthlylogs")
        window.location.href = "/monthlylogs"

    }
    const handleLogin = (event) =>{
        event.preventDefault()
        const form = document.querySelector("form")
        if (!form.checkValidity()){
            form.reportValidity()
            return
        }
        const username = document.querySelector("#username").value
        const password = document.querySelector("#passwd").value
        
        const headers = {
            "Content-Type": "application/json"
        }
        fetch(`${apiurl}/login`,{
            method:"POST",
            body : JSON.stringify({username,password}),
            headers:headers
        }).then(resp=>resp.json())
        .then(data=>{
            if (data.token){
                localStorage.setItem("token",`Bearer ${data.token}`)
                window.location.href = "/monthlylogs"
            }else{
                document.querySelector(".prompt").innerHTML = "Email or password errror"
            }
        })
        .catch(err=>console.log(err))
    }

  return (
    <div className='make-center' style={{display:"flex",height:"100vh"}}>
        <div className="" style={{border:"2px solid #aaa",height:"400px",width:"40%",margin:"auto"}}>
        <form className='' style={{display:"flex",flexDirection:"column",alignItems:"center",justifyContent:"center",width:"80%",margin:"auto",marginTop:"8rem"}}>
        <div className="input-group flex-nowrap mb-3">
            <span className="input-group-text" id="addon-wrapping">@</span>
            <input type="text" className="form-control" id="username" placeholder="Username" aria-label="Username" aria-describedby="addon-wrapping" required />
        </div>
        <div className="input-group flex-nowrap mb-3">
            <span className="input-group-text" id="addon-wrapping">@</span>
            <input type="password" className="form-control" id="passwd" placeholder="Password" aria-label="Username" aria-describedby="addon-wrapping" required />
        </div>
  <button type="submit" className="btn btn-primary" onClick={handleLogin}>Submit</button>
</form>
    <div className="prompt"></div>
        </div>
        
    </div>
  )
}
